/* Projects Page Styles */
.projects-page {
    padding: 5rem 0;
  }
  
  .projects-header {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  .projects-title {
    font-size: 3rem;
    font-weight: 700;
  }
  
  .projects-grid {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
    display: grid;
    gap: 2rem;
  }
  
  /* Project Card Styles */
  .project-card {
    background: white;
    border-radius: 1rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease;
  }
  
  .project-card:hover {
    transform: translateY(-5px);
  }
  
  .project-content {
    display: flex;
    align-items: center;
  }
  
  .project-info {
    flex: 1;
    padding: 2rem;
  }
  
  .project-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }
  
  .project-description {
    color: var(--dark);
    opacity: 0.8;
    line-height: 1.7;
    margin-bottom: 1.5rem;
  }
  
  .project-tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .tech-tag {
    background: var(--gradient);
    color: white;
    padding: 0.25rem 0.75rem;
    border-radius: 1rem;
    font-size: 0.875rem;
  }
  
  .project-links {
    display: flex;
    gap: 1rem;
  }
  
  .project-link {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    text-decoration: none;
    color: var(--dark);
    font-weight: 600;
    transition: color 0.3s ease;
  }
  
  .project-link:hover {
    color: var(--primary);
  }
  
  .project-link i {
    font-size: 1.25rem;
  }
  
  .project-image {
    width: 300px;
    height: 400px;
    overflow: hidden;
  }
  
  .project-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .project-card:hover .project-img {
    transform: scale(1.05);
  }
  
  .project-img-placeholder {
    width: 100%;
    height: 100%;
    background-color: var(--light);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    color: var(--dark);
    opacity: 0.5;
  }
  
  /* CTA Section */
  .projects-cta {
    text-align: center;
    padding: 5rem 0;
    margin-top: 4rem;
    background: var(--gradient);
    color: white;
  }
  
  .cta-title {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 2rem;
  }
  
  /* Responsive Styles */
  @media (max-width: 992px) {
    .project-content {
      flex-direction: column;
    }
  
    .project-image {
      width: 100%;
      height: 300px;
      order: -1;
    }
  }
  
  @media (max-width: 768px) {
    .projects-page {
      padding: 3rem 0;
    }
  
    .projects-title {
      font-size: 2.5rem;
    }
  
    .project-info {
      padding: 1.5rem;
    }
  
    .cta-title {
      font-size: 2rem;
    }
  }
  
  @media (max-width: 576px) {
    .projects-title {
      font-size: 2rem;
    }
  
    .project-links {
      flex-direction: column;
    }
  
    .project-image {
      height: 250px;
    }
  }
  
  /* Animation for card hover */
  @keyframes cardHover {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-5px);
    }
  }
  
  .project-card {
    animation: cardHover 0.3s ease forwards;
  }